*{
  margin: 0px;
  padding: 0px;
}
.App{
 display:block;
margin: 0px auto;
}
body{
  background:url('/src/img/cancha.jpg') ;
  background-size: cover;
  /* fallback for old browsers */
  background-attachment: fixed;
}

.leagues{
  display: grid;
justify-content: center;
  margin: 0px auto;
  margin-top: 1%;
  gap:10px;
  grid-template-columns: repeat(6,160px);
  grid-template-rows:50% 50%
}
.leagues img{
  width: 100%;
  margin: 0px auto ;
  height: 100%;
  border: 1px solid rgb(255, 255, 255);
  border-radius: 5px;
 }

 .leagues img:hover{
  box-shadow: 1px 1px 18px 2px rgba(240,240,240,1);
 }

 
form{
   display:flex;
   width: 100%;
   height: 50px;
   justify-content: center;
   margin-top: 4%;
 }
 form .input-contain{
   width: 51%;
   height: 100%;
   background-color:rgb(8, 5, 5,0.5);
   display: flex;
   border-radius: 25px;
   border: 1px solid gray;
 }
 .input-contain .material-icons{
   font-size: 180%;
   line-height: 180%;
   margin-right: 10px;
   margin-left: 10px;
   color: gray;
 }
 .input-contain input{
   width: 80%;
   height: 100%;
   text-align: center;
   outline-style: none;
   color: white;
   background-color: transparent;
   border: none;
   font-size: 140%;
   
 }
.buttonInput{
   width: 10%;
   margin-left: 1%;
   height: 52px;
   line-height: 52px;
   border-radius: 25px;
   color: RGB(255,255,255);
   border: 1px solid rgb(41, 131, 206); ;
   background-color: rgb(50, 148, 228);
   font-size: 100%;
   font-weight: 400;
   font-family:Verdana, Geneva, Tahoma, sans-serif
 }
 .buttonInput:hover{
   background-color: RGB(255,255,255);
   color: rgb(48, 47, 47);
   border: none;
 }
 



@media(max-width:1041px){
  .leagues{
  grid-template-columns: repeat(4,160px);
  grid-template-rows: repeat(3, 1fr);
  }
  form{
  margin-bottom: 50px;
  }
}

@media(max-width:768px){
 .leagues{
    grid-template-columns: repeat(4,100px);
    grid-template-rows: repeat(3, 120px);
  }
 form{
    height: 40px;
    margin-top: 50px;
  }
  form .input-contain{
    width: 309px;
    border-radius: 3px;
  }
  .input-contain .material-icons{
    font-size: 150%;
    line-height: 180%;
  }
  .input-contain input{
    
   font-size: 110%;
  }
  .buttonInput{
    width: 115px;
    height: 42px;
    font-size: 80%;
    line-height: 42px;
    border-radius: 3px;
  }
}
@media(max-width:456px){
  .leagues{
    grid-template-columns: repeat(3,120px);
    grid-template-rows: repeat(4, 140px);
  }
  form .input-contain{
    width: 280px;
  }
  .buttonInput{
    width: 95px;
  }
}
@media(max-width:398px){
  .leagues{
    grid-template-columns: repeat(3,100px);
    grid-template-rows: repeat(4, 120px);
  }
  form .input-contain{
    width: 250px;
    font-size: 15px;
  }
  .buttonInput{
    width: 70px;
    font-size: 13px;
  }
}
@media(max-width:336px){
  .leagues{
    grid-template-columns: repeat(3,90px);
    grid-template-rows: repeat(4, 110px);
  }
  form .input-contain{
    width: 240px;
    
  }
  .buttonInput{
    width: 60px;
    font-size: 13px;
  }
}